import React, { useEffect } from 'react';
import './Stars.css';

function Stars() {
  useEffect(() => {
    const createStar = () => {
      const star = document.createElement('div');
      star.classList.add('star');

      //     
      const starSize = Math.random() * 3 + 1; //  1  4 
      star.style.width = `${starSize}px`;
      star.style.height = `${starSize}px`;
      star.style.left = `${Math.random() * window.innerWidth}px`;
      star.style.top = `${Math.random() * window.innerHeight}px`;

      //       
      const xEnd = 1000 * (Math.random() - 0.5); //  -1000  1000   X
      const yEnd = 1000 * (Math.random() - 0.5); //  -1000  1000   Y
      const scaleEnd = Math.random() * 1.5 + 0.5; //   0.5  2

      //    
      star.style.setProperty('--x-end', `${xEnd}px`);
      star.style.setProperty('--y-end', `${yEnd}px`);
      star.style.setProperty('--scale-end', scaleEnd);

      //  
      star.style.animation = `fly ${Math.random() * 3 + 2}s linear forwards`;

      //          
      document.body.appendChild(star);
      star.addEventListener('animationend', () => {
        star.remove();
      });
    };

    //    100 
    const interval = setInterval(createStar, 100);

    //     
    return () => clearInterval(interval);
  }, []); //  useEffect ,        

  return null; //    ,        
}

export default Stars;
